:root {
  --text-color: #00acdc;
}

body {
  background-color: azure;
  height: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.main-nav {
  box-sizing: border-box;
  margin-left: auto;
}

.nav-list {
  text-align: center;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-list .nav-item {
  text-transform: uppercase;
  box-sizing: border-box;
  font-size: 12px;
  display: inline-block;
}

.nav-list .nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav-link.is-active, .nav-link:hover {
  color: var(--text-color);
}

.nav-link.is-active:after {
  content: " ";
  color: var(--text-color);
  background-color: var(--text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
